import React, {useMemo} from 'react';
import {Drawer, PageTitle, NumberFormat} from '../../units';
import {Card, Row, Col} from 'antd';
import {useDebitTransactions} from '../../hooks';
import {ReactTable} from '../../units';
import { ReactQueryDevtools } from 'react-query-devtools';
import {formatDate} from '../../helpers';


export default function ViewDebitTransactions() {
    const { status, data, error, isFetching } = useDebitTransactions();

    const actualData = data? data.data: [];

    const columns = useMemo(() => [
        {
            id: 'date',
            Header: "Date",
            accessor: d => formatDate(d.createdAt),
            Cell: d => <span>{d.value}</span>

        },
        {
            Header: 'Account Name',
            accessor: 'user.name'
        },
        {
            Header: 'Debit Means',
            accessor: 'debitMeans'
        },
        {
            Header: 'Network',
            accessor: 'network'
        },
        {
            Header: 'Phone',
            accessor: 'phoneNumber'
        },
        {
            Header: 'Recipient Name',
            accessor: 'recipientName'
        },
        {
            Header: 'Amount',
            accessor: 'amount',
            Cell: d => <NumberFormat value={d.value} />
        },
        {
            Header: 'Balance',
            accessor: 'currentAmount',
            Cell: d => <NumberFormat value={d.value} />

        },
        {
            Header: 'Status',
            accessor: 'status'
        }
    ], actualData);


    return (
        <Drawer selectedKey="20">
            
        <Card>
        <Row gutter={24}>
                    <Col span={20}>
            <PageTitle title="Debit Transactions" />
            </Col>
               <Col span={4} style={{padding: '10px'}}>
                {/* <ButtonLink style={{borderRadius: '5px'}} type="primary" to="/users/create" label="New User" /> */}
                </Col>
            </Row>

            <ReactTable 
            columns={columns}
            data = {actualData}
            filterable={true}
            />
        </Card>
        <ReactQueryDevtools initialIsOpen />
        </Drawer>
    )
}
